"use client";

import { useEffect } from "react";
import { useParams } from "next/navigation";
import Link from "next/link";
import { useGlobalState } from "../../services/globalstate";

export default function UserPostsPage() {
  const { id } = useParams(); // Get user ID from URL
  const { users, posts, loading, error, fetchingUserPosts, } = useGlobalState();

  // Fetch users list on mount (only if not already fetched)
  // useEffect(() => {
  //   if (users.length === 0) fetchingUsers();
  // }, []);

  // Fetch user posts when `id` changes
  useEffect(() => {
    fetchingUserPosts(id);
  }, [id]);

  // Find the selected user from global state
  const selectedUser = users.find((user) => user.id === Number(id));

  return (
    <div className="container mx-auto p-4">
      <Link href="/users" className="text-blue-500 underline">
        ‚Üê Back to Users
      </Link>

      <h1 className="text-2xl font-bold my-4">
        Posts by {selectedUser ? selectedUser.name : "User"}
      </h1>

      {loading ? (
        <p>Loading posts...</p>
      ) : error ? (
        <p className="text-red-500">{error}</p>
      ) : posts.length > 0 ? (
        <ul className="mt-2">
          {posts.map((post) => (
            <li key={post.id} className="border p-2 rounded-lg mb-2">
              <h3 className="font-semibold">{post.title}</h3>
              <p>{post.body}</p>
            </li>
          ))}
        </ul>
      ) : (
        <p>No posts found.</p>
      )}
    </div>
  );
}
